*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content{
  height: 100%;
}

.navbar{
  background-color: rgb(243, 235, 235) !important;
}

.navbar-brand{
  font-size: 30px;
  font-weight: bolder;
}

.nav-item{
  margin: 10px;
}

.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

/* Login Page */
.login-heading{
  text-align: center;
  font-size: 50px;
  margin-bottom: 50px;
  color: rgb(49, 43, 43)
}

.login-subheading{
  text-align: center;
  font-size: 16px;
  margin-bottom: 50px;
  color: rgb(138, 8, 8);
  font-weight: bolder;
}

.login-form{
  background-image: url(../public/pic3.jpg);
  background-size: cover;
  border: 1px solid rgb(197, 188, 188);
  padding: 70px;
  border-radius: 20px;
}

.register-form{
  background-image: url(../public/pic4.jpg);
  background-size: cover;
  border: 1px solid rgb(197, 188, 188);
  padding: 50px 70px;
  border-radius: 20px;
}
.btn-primary{
  background-color: rgb(138, 8, 8);
  border: none;
}
.btn-primary:hover{
  background-color: rgb(71, 13, 13);
}
.login-link{
  color: antiquewhite;
  font-size: 15px;
}
.login-link:hover{
  color: rgb(138, 8, 8);
  font-weight: bolder;
}

/* Transection Page */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}
/* Transection Page */

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
  color: black
}

.inactive-icon{
  color: gray
}

.switch-icons{
  border: 1px solid rgba(0, 0, 0, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.home-maindiv{
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 767px) {
  .home-maindiv {
      flex-direction: column;
      padding: 20px;
  }
}

.categories-heading{
  font-size: 20px;
}